import React, { Component } from 'react'
import './App.css';
import TodoItem from './components/TodoItem';
import imgCheckAll from './img/check-all.svg';
import classNames  from 'classnames';
const filterByStatus = (todoItem= [],status="all") =>{
 switch (status){
   case 'active': return todoItem.filter(todo=>todo.isCompleted === false)
   case 'complete': return todoItem.filter(todo=>todo.isCompleted === true)
   default : return todoItem;
 }
}
export default class App extends Component {
constructor() {
super();
this.state ={ 
  itemInput: '',
  todoItem:[],
  status: 'all',
  idEditting: null
}}
 
  onItemClick = (item) =>{
    return () =>{
      const {todoItem} = this.state;
      const todos = todoItem.map(todo => {
        if(todo === item) {
          todo.isCompleted = !todo.isCompleted;
        }
        return todo;
      })
      this.setState({
        todoItem: todos
      })
      // localStorage.setItem("todos",JSON.stringify(todos));
    }
}
  onKeyUp = (e) =>{
    const key = e.keyCode;
    if(key === 13){
      let valueIn = e.target.value;
      if(!valueIn){
        return;
      }
      this.setState({
        itemInput: '',
        todoItem: [
        {id: new Date().valueOf() ,title: valueIn,isCompleted: false},
         ...this.state.todoItem
        ],
        idEditting: null
      })
    }
  }
  onChangeValue=(e) =>{
      this.setState({
        itemInput: e.target.value
      })
  }
  handleDeleteItem = (item) =>{
    return () =>{
      const {todoItem} = this.state;
      const index = todoItem.findIndex(todo => todo === item);
      this.setState({
        todoItem: [
          ...todoItem.slice(0,index),
          ...todoItem.slice(index+1)
        ]
      })
    }
  }
  handleClearAll = () =>{
    const {todoItem} = this.state;
    this.setState({
      todoItem: todoItem.filter(todo=> todo.isCompleted === false)
    })
  }
  onCompleteAll =()=>{
    const {todoItem}= this.state;
    const countTrue = todoItem.filter(todo=>todo.isCompleted === true);
    if(countTrue.length===todoItem.length){
      this.setState({
        todoItem: todoItem.map(todo=>{
          return {...todo,isCompleted:false}
        })
      })
    }
    else {
      this.setState({
        todoItem: todoItem.map(todo=>{
          return {...todo,isCompleted:true}
        })
      })
    }
  }
  onFilter = (status) =>{
   return () =>{
    this.setState({
      status
    })
   }
  }
  componentWillMount(){
    this.setState({
      status : "all",
    })
  }
  componentDidMount(){
    const data = JSON.parse(localStorage.getItem("todos"));
    if(data){
      this.setState({
        todoItem: [...data]
      })
    }
  }
  componentWillUpdate(nextprops,nextstate){
    localStorage.setItem("todos",JSON.stringify(nextstate.todoItem));
  }
  onEdittingId = (id) =>{
    return () =>{
      this.setState({
        idEditting: id
      })
    }
  }
  handelEditing= (id,content)=>{
    const {todoItem}= this.state;
    const arr = todoItem.map(todo=>{
      if(todo.id === id) {
        return {...todo,title: content}
      } else return todo
    })
    this.setState({
      todoItem: arr,
      idEditting: null
    })
  }
  render() {
    const {todoItem,itemInput,status,idEditting} = this.state;
    let countItem= todoItem.filter(todo=>todo.isCompleted===true).length;
    return (
      <div className="App">
      <div className="Container">
            <h2 className="Logo">todos</h2>
            <header className="Header">
              <img src = {imgCheckAll} onClick={this.onCompleteAll} alt="checkAll" className="Image__CheckAll"></img>
              <input type="text" 
              className="InputBox"
              placeholder="What need to be done ?"
              onKeyUp={this.onKeyUp}
              onChange={this.onChangeValue}
              value={itemInput}
              />
            </header>
            <TodoItem todoItem={filterByStatus(todoItem,status)} 
            onClick={this.onItemClick}
             onDelete={this.handleDeleteItem} 
             idEditting={idEditting}
             onEdittingId = {this.onEdittingId}
             handelEditing={this.handelEditing}
             />
            {/* { 
            todoItem.length === 0 && <div className="nothing">No Todo</div>
            } */}
            <footer className="footer">
                <span className="todo-count">{countItem} items left</span>
              <ul className="filters">
              <li className= {classNames('filters__option', {'fil-active': status==="all"})} onClick={this.onFilter("all")}>All</li>
              <li className= {classNames('filters__option', {'fil-active': status==="active"})} onClick={this.onFilter("active")}>Active</li>
              <li className= {classNames('filters__option', {'fil-active': status==="complete"})} onClick={this.onFilter("complete")}>Completed</li>
              </ul>
              <span className= "clear-complete" onClick={this.handleClearAll}>Clear All Complete</span>
            </footer>
       </div>
      <h4 className="copyright">@ By BronzeV- Double-click to edit a todo</h4>
      </div>
    )
  }
}

