import React, { Component } from 'react'
import './TodoItem.css';
import classNames from 'classnames';
import checkImg from '../img/check.svg';
import checkDone from '../img/check-done.svg';
import closeItem from '../img/close.svg';
class TodoItem extends Component {
    constructor(){
        super();
        this.state= {
            textEdditing: ''
        }
    }
    onChange = (e) =>{
        this.setState({
            textEdditing: e.target.value
        })
    }
    onKeyUp = (id,e) =>{
        const key = e.keyCode;
        if(key === 13){
          let value = e.target.value;
          if(!value){
            return;
          }
            const {handelEditing} = this.props;
            handelEditing(id,value)
            this.setState({
            textEdditing: ""
          })
    }}
    onBlur = (id,e) =>{
        const {handelEditing} = this.props;
        let value = e.target.value;
        if (!value){
        return;
        }
          handelEditing(id,value)
          this.setState({
          textEdditing: ""
        })
    }
    render() {
    const {todoItem, onClick,onDelete, idEditting,onEdittingId } = this.props;
    const {textEdditing}= this.state;
        return (
            todoItem.map((item,index) => {
                return <div
                className={classNames('Todo__Item', {'Todo__Item--done': item.isCompleted  })}
                key={index} >
                   <img className="check-img" src={item.isCompleted ? checkDone : checkImg} alt="imgcheck" onClick = {onClick(item)} />
                   <p className={classNames('todo__title', {'todo--hide': idEditting === item.id  })} 
                   onDoubleClick = {onEdittingId(item.id)}
                   >{item.title}
                   </p>
                   { idEditting === item.id &&
                   <input type="text" 
                   value = {textEdditing}
                   onChange ={this.onChange}
                   onBlur={(e)=>{this.onBlur(item.id,e)}}
                   onKeyUp={(e)=>{this.onKeyUp(item.id,e)}}
                   className="input__edit" />
                    }
                   <img className={classNames('close', {'todo--hide': idEditting === item.id })}  src={closeItem} alt="close" onClick = {onDelete(item)}/>
               </div>
            })          
        )
    }
}
export default TodoItem;